html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-size: 2rem;
  font-weight: 700;
  background-color: var(--main-bg);
}
@media (min-width: 40em) {
  body {
    height: 100vh;
    display: grid;
    align-items: center;
  }
}

.theme1 {
  --text-color: hsl(0, 0%, 100%);
  --main-bg: hsl(222, 26%, 31%);
  --toggle-keypad-bg: hsl(223, 31%, 20%);
  --screen-bg: hsl(224, 36%, 15%);
  --key-bg: hsl(225, 21%, 49%);
  --key-shadow: hsl(224, 28%, 35%);
  --ky-bg-toggle: hsl(6, 63%, 50%);
  --key-shadow-equal: hsl(6, 70%, 34%);
  --num-key-bg: hsl(30, 25%, 89%);
  --num-key-shadow: hsl(28, 16%, 65%);
  --num-key-text: hsl(221, 14%, 31%);
  --num-key-text-equal: hsl(0, 0%, 100%);
}

.theme2 {
  --text-color: hsl(60, 10%, 19%);
  --main-bg: hsl(0, 0%, 90%);
  --toggle-keypad-bg: hsl(0, 5%, 81%);
  --screen-bg: hsl(0, 0%, 93%);
  --key-bg: hsl(185, 42%, 37%);
  --key-shadow: hsl(185, 58%, 25%);
  --ky-bg-toggle: hsl(25, 98%, 40%);
  --key-shadow-equal: hsl(25, 99%, 27%);
  --num-key-bg: hsl(30, 25%, 89%);
  --num-key-shadow: hsl(35, 11%, 61%);
  --num-key-text: hsl(60, 10%, 19%);
}

.theme3 {
  --text-color: hsl(52, 100%, 62%);
  --main-bg: hsl(268, 75%, 9%);
  --toggle-keypad-bg: hsl(268, 71%, 12%);
  --screen-bg: hsl(268, 71%, 12%);
  --key-bg: hsl(281, 89%, 26%);
  --key-shadow: hsl(285, 91%, 52%);
  --ky-bg-toggle: hsl(176, 100%, 44%);
  --key-shadow-equal: hsl(177, 92%, 70%);
  --num-key-bg: hsl(268, 47%, 21%);
  --num-key-shadow: hsl(290, 70%, 36%);
  --num-key-text: hsl(52, 100%, 62%);
  --num-key-text-equal: hsl(198, 20%, 13%);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --text-color: hsl(0, 0%, 100%);
  --main-bg: hsl(222, 26%, 31%);
  --toggle-keypad-bg: hsl(223, 31%, 20%);
  --screen-bg: hsl(224, 36%, 15%);
  --key-bg: hsl(225, 21%, 49%);
  --key-shadow: hsl(224, 28%, 35%);
  --ky-bg-toggle: hsl(6, 63%, 50%);
  --key-shadow-equal: hsl(6, 70%, 34%);
  --num-key-bg: hsl(30, 25%, 89%);
  --num-key-shadow: hsl(28, 16%, 65%);
  --num-key-text: hsl(221, 14%, 31%);
  --num-key-text-equal: hsl(0, 0%, 100%);
}

@media (prefers-color-scheme: light) {
  :root {
    --text-color: hsl(60, 10%, 19%);
    --main-bg: hsl(0, 0%, 90%);
    --toggle-keypad-bg: hsl(0, 5%, 81%);
    --screen-bg: hsl(0, 0%, 93%);
    --key-bg: hsl(185, 42%, 37%);
    --key-shadow: hsl(185, 58%, 25%);
    --ky-bg-toggle: hsl(25, 98%, 40%);
    --key-shadow-equal: hsl(25, 99%, 27%);
    --num-key-bg: hsl(30, 25%, 89%);
    --num-key-shadow: hsl(35, 11%, 61%);
    --num-key-text: hsl(60, 10%, 19%);
    --num-key-text-equal: hsl(0, 0%, 100%);
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --text-color: hsl(52, 100%, 62%);
    --main-bg: hsl(268, 75%, 9%);
    --toggle-keypad-bg: hsl(268, 71%, 12%);
    --screen-bg: hsl(268, 71%, 12%);
    --key-bg: hsl(281, 89%, 26%);
    --key-shadow: hsl(285, 91%, 52%);
    --ky-bg-toggle: hsl(176, 100%, 44%);
    --key-shadow-equal: hsl(177, 92%, 70%);
    --num-key-bg: hsl(268, 47%, 21%);
    --num-key-shadow: hsl(290, 70%, 36%);
    --num-key-text: hsl(52, 100%, 62%);
    --num-key-text-equal: hsl(198, 20%, 13%);
  }
}
:root {
  --font-body: "Spartan", sans-serif;
}

.container {
  max-width: 33.75rem;
  margin-inline: auto;
  padding-inline: 1.5625rem;
}
@media (min-width: 40em) {
  .container {
    padding: 0;
  }
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-block: 0;
  line-height: 1;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.875rem;
  padding-top: 1.875rem;
}
@media (min-width: 40em) {
  header {
    padding: 0;
  }
}
header h1 {
  font-size: 1.9375rem;
  color: var(--text-color);
}
header .theme {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  margin-bottom: 0;
}
header .theme .labels {
  width: 4.5rem;
  margin-left: auto;
  display: flex;
  justify-content: space-around;
  gap: 1.0625rem;
  margin-bottom: 0.3125rem;
  padding-inline: 0.46875rem;
  font-size: 0.75rem;
  color: var(--text-color);
}
header .theme .toggle {
  display: flex;
  align-items: center;
  gap: 1.6875rem;
  font-size: 0.75rem;
  text-transform: uppercase;
  color: var(--text-color);
}
header .theme .radio-btn {
  display: flex;
  align-items: center;
  border-radius: 1.25rem;
  padding-inline: 0.46875rem;
  padding-block: 0.25rem;
  background-color: var(--toggle-keypad-bg);
}
header .theme .radio-btn input[type=radio] {
  border-radius: 50%;
  padding: 0;
  margin: 0;
  appearance: none;
  height: 1.1875rem;
  width: 1.1875rem;
  cursor: pointer;
}
header .theme .radio-btn input[type=radio]:checked {
  border: none;
  background-color: var(--ky-bg-toggle);
  border-radius: 50%;
  height: 1.1875rem;
  width: 1.1875rem;
}

main form {
  margin-bottom: 1.5625rem;
  font-size: 0;
}
@media (min-width: 40em) {
  main form {
    margin-bottom: 1.4375rem;
  }
}
main form label {
  position: relative;
  display: block;
  width: fit-content;
  height: fit-content;
}
main form label .display-field {
  width: 100%;
  height: 100%;
  appearance: none;
  outline: none;
  border: none;
  border-radius: 0.625rem;
  padding-inline: 1.6875rem;
  padding-block: 1.625rem;
  background-color: var(--screen-bg);
  font-size: 1.875rem;
  font-weight: 700;
  font-family: var(--font-body);
  text-align: right;
  color: var(--text-color);
}
@media (min-width: 40em) {
  main form label .display-field {
    font-size: 2.75rem;
    padding-inline: 2.3125rem;
    padding-block: 2.3125rem;
  }
}
main form label .exp-container {
  position: absolute;
  top: 5%;
  display: block;
  height: 1.25rem;
  width: 100%;
  appearance: none;
  outline: none;
  border: none;
  border-radius: 100%;
  padding-inline: 2.1875rem;
  font-size: 0.75rem;
  font-weight: 700;
  font-family: var(--font-body);
  text-align: right;
  color: var(--text-color);
  background-color: var(--screen-bg);
}
@media (min-width: 40em) {
  main form label .exp-container {
    font-size: 0.9375rem;
    padding-inline: 2.8125rem;
  }
}
main .buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.8125rem;
  margin-bottom: 1.875rem;
  border-radius: 0.625rem;
  padding-inline: 1.5rem;
  padding-block: 1.5rem;
  background-color: var(--toggle-keypad-bg);
}
@media (min-width: 40em) {
  main .buttons {
    gap: 1.4375rem;
    margin-bottom: 0;
    padding-inline: 2rem;
    padding-block: 2rem;
  }
}
main .buttons button {
  border: none;
  border-radius: 0.3125rem;
  box-shadow: 0 0.3125rem var(--num-key-shadow);
  padding-inline: 1.1875rem;
  padding-block: 1.1875rem;
  font-size: 1.5rem;
  font-weight: 700;
  font-family: var(--font-body);
  color: var(--num-key-text);
  background-color: var(--num-key-bg);
  cursor: pointer;
}
@media (min-width: 40em) {
  main .buttons button {
    border-radius: 0.625rem;
    padding-block: 0.9375rem;
    font-size: 1.8125rem;
  }
}
main .buttons .btn-click {
  animation-name: button-click;
  animation-duration: 100ms;
  animation-timing-function: ease;
}
main .buttons .del {
  font-size: 1rem;
  text-transform: uppercase;
  background-color: var(--key-bg);
  box-shadow: 0 0.3125rem var(--key-shadow);
  color: var(--white);
}
@media (min-width: 40em) {
  main .buttons .del {
    font-size: 1.1875rem;
  }
}
main .buttons .reset {
  grid-column: 1/3;
  text-transform: uppercase;
  font-size: 1rem;
  background-color: var(--key-bg);
  box-shadow: 0 0.3125rem var(--key-shadow);
  color: var(--white);
}
@media (min-width: 40em) {
  main .buttons .reset {
    font-size: 1.1875rem;
  }
}
main .buttons .equal {
  grid-column: 3/5;
  box-shadow: 0 0.3125rem var(--key-shadow-equal);
  color: var(--num-key-text-equal);
  background-color: var(--ky-bg-toggle);
}

@keyframes button-click {
  from {
    box-shadow: 0 0.3125rem var(--num-key-shadow);
  }
  to {
    transform: translateY(10%);
    box-shadow: none;
  }
}